<template>
    <span class="location" @click="selectCity">
        <span class="location_city" v-bind:cityId='examCityList.currentCity.CityId'>{{examCityList.currentCity.CityName}}</span>
    </span>
</template>

<script>


import { mapGetters, mapActions } from "vuex";
export default {
    props: {
        locationSuccessCallBack: { type: Function, default: (result) => { alert(result) } },
        locationErrorCallBack: { type: Function, default: (result) => (alert('失败')) },

    },

    methods: {
        ...mapActions([
            "getExamCityList",
            "setCurrentExamCity"
        ]),
        showCityInfo(success, error) {
            let self = this;
            //如果已经定位过，再次返回本页面不用再次定位
            if (self.examCityList.currentCity.CityId) {
                self.locationSuccessCallBack(self.examCityList.currentCity);
            }
            else {
                var citysearch = new AMap.CitySearch();
                //自动获取用户IP，返回当前城市
                citysearch.getLocalCity(function (status, result) {
                    let city = { cityId: null, cityName: '' }
                    if (status === 'complete' && result.info === 'OK') {
                        if (result && result.city && result.bounds) {
                            let cityinfo = result.city;
                            let citybounds = result.bounds;
                            city.cityName = cityinfo;
                            //todo 根据配置获取城市ID
                            city.cityId = 511;
                        }
                    } else {

                    }
                    self.getExamCityList({ cityId: city.cityId }).then(
                        () => {
                            self.locationSuccessCallBack(self.examCityList.currentCity);
                        }
                    )
                });
            }
        },
        showLocationInfo() {
            let self = this;
            let city = { cityId: null, cityName: '' }
            var map, geolocation;

            //加载地图，调用浏览器定位服务
            map = new AMap.Map('container', {
                resizeEnable: true
            });
            map.plugin('AMap.Geolocation', function () {
                geolocation = new AMap.Geolocation({
                    enableHighAccuracy: true,//是否使用高精度定位，默认:true
                    timeout: 10000,          //超过10秒后停止定位，默认：无穷大
                    buttonOffset: new AMap.Pixel(10, 20),//定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
                    zoomToAccuracy: true,      //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
                    buttonPosition: 'RB'
                });
                // map.addControl(geolocation);
                geolocation.getCurrentPosition();
                AMap.event.addListener(geolocation, 'complete', onComplete);//返回定位信息
                AMap.event.addListener(geolocation, 'error', onError);      //返回定位出错信息
                self.showCityInfo();
            });
            //解析定位结果
            function onComplete(data) {
                // alert("success");
                // console.log(data);
                // if (data.accuracy) {
                //     alert(1);
                //     AMap.service('AMap.Geocoder', function () {//回调函数
                //         //实例化Geocoder
                //         geocoder = new AMap.Geocoder({
                //             city: "010"//城市，默认：“全国”
                //         });
                //         var lnglatXY = [data.position.getLng(), data.position.getLat()];//地图上所标点的坐标
                //         geocoder.getAddress(lnglatXY, function (status, result) {
                //             alert(result);
                //             if (status === 'complete' && result.info === 'OK') {
                //                 let cityinfo = result.city;
                //                 let citybounds = result.bounds;
                //                 city.cityName = cityinfo;
                //                 //todo 根据配置获取城市ID
                //                 city.cityId = 511;
                //             } else {
                //                 //获取地址失败
                //                 // self.getExamCityList({ cityId: city.cityId }).then(
                //                 //     () => {
                //                 //         self.locationSuccessCallBack(self.examCityList.currentCity);
                //                 //     }
                //                 // )
                //             }
                //         });

                //     })
                // }
                // else {

                // }//如为IP精确定位结果则没有精度信息


            }
            //解析定位错误信息
            function onError(data) {
                // alert("error");
                // self.getExamCityList({ cityId: city.cityId }).then(
                //     () => {
                //         self.locationSuccessCallBack(self.examCityList.currentCity);
                //     }
                // )
            }
        },
        selectCity() {
            let self = this;
            let data = self.examCityList.dataList.map((item) => {
                return { value: item.CityId, text: item.CityName };
            });
            console.log(self.examCityList);
            var picker = new mui.PopPicker();
            picker.setData(data);
            let currentCity = self.examCityList.currentCity;
            picker.pickers[0].setSelectedValue(currentCity.CityId, 2000);
            picker.show(function (SelectedItem) {
                if (Array.isArray(SelectedItem) && SelectedItem.length > 0) {
                    console.log(SelectedItem);
                    self.setCurrentExamCity({ currentCity: { CityId: SelectedItem[0].value, CityName: SelectedItem[0].text } });
                    () => {
                        self.locationSuccessCallBack(self.examCityList.currentCity);
                    }
                }

            })
        }
    },
    computed: {
        ...mapGetters([
            "examCityList"
        ])
    },
    created() {
        // this.showCityInfo();
        this.showLocationInfo();
    }

}
</script>

<style lang="less" scoped>
@import (reference) "../../assets/css/base.less";
.location {
  font-size: 26/@r;
  &:after {
    display: inline-block;
    width: 16/@r;
    height: 14/@r;
    background: url("../../assets/images/icon.png") -151/@r -10/@r;
    background-size: 640/@r 200/@r;
    vertical-align: middle;
    margin-left: 10/@r;
    content: "";
  }
}
</style>




