<template>
    <div>
        <common-header title="考场列表" />
        <div class="location_warp">
            <location :locationSuccessCallBack='locationSuccessCallBack' />
            <search placeholder="搜索考场/候选人" v-bind:searchClick="searchClick" />
        </div>
        <ul id="examlist" >
            <examitem v-for="(item,index) in examRoomList" v-bind:key="item.RoomID" :examinfo='item' :itemClick="redirectResumeList" />
            <examitem v-for="(item,index) in examRoomList" v-bind:key="item.RoomID" :examinfo='item' :itemClick="redirectResumeList" />
            <examitem v-for="(item,index) in examRoomList" v-bind:key="item.RoomID" :examinfo='item' :itemClick="redirectResumeList" />
            <examitem v-for="(item,index) in examRoomList" v-bind:key="item.RoomID" :examinfo='item' :itemClick="redirectResumeList" />
            <examitem v-for="(item,index) in examRoomList" v-bind:key="item.RoomID" :examinfo='item' :itemClick="redirectResumeList" />
            <examitem v-for="(item,index) in examRoomList" v-bind:key="item.RoomID" :examinfo='item' :itemClick="redirectResumeList" />
        </ul>
    </div>
</template>

<script>
import search from './common/SearchInput';
import location from './common/Location';
import examitem from './common/ExaminationItem';
import Header from './common/Header';
import { mapGetters, mapActions } from "vuex";
export default {
    methods: {
        ...mapActions([
            "getExamRoomList",
        ]),
        locationSuccessCallBack: function (result) {
            this.getExamRoomList().then(() => console.log("fin"));
            let self = this;
            mui.init({
                pullRefresh: {
                    container: "examlist",//待刷新区域标识，querySelector能定位的css选择器均可，比如：id、.class等
                    up: {
                        height: 50,//可选.默认50.触发上拉加载拖动距离
                        auto: true,//可选,默认false.自动上拉加载一次
                        contentrefresh: "正在加载...",//可选，正在加载状态时，上拉加载控件上显示的标题内容
                        contentnomore: '没有更多数据了',//可选，请求完毕若没有更多数据时显示的提醒内容；
                        callback:()=>{alert(1);self.getExamRoomList()} //必选，刷新函数，根据具体业务来编写，比如通过ajax从服务器获取新数据；
                    }
                }
            });
        },
        searchClick() {
            this.$router.push({ name: 'ExaminationSearch' });
        },
        redirectResumeList(roomID) {
            this.$router.push({ name: 'ResumeList', query: { roomid: roomID } });
        }
    },
    computed: {
        ...mapGetters([
            "examRoomList",
        ])
    },
    created() {

    },
    components: {
        search,
        location,
        'common-header': Header,
        examitem
    }
}
</script>
<style lang="less" scoped>
@import (reference) "../assets/css/base.less";
body {
  background: #f3f6f9;
}
.location_warp {
    margin-top: 2.2rem;
  background: #fff;
  padding: 21/@r 0 21/@r 30/@r;
  box-sizing: border-box;
}
</style>




